<!--  -->
<template>
  <div id="btnBar" class="btn-bar">
    <span v-for="(item, index) in list" :key="index">
      <!------------------------------------- slot returnType=1  2 json ------------------------------------>
      <span>
        <slot v-if="item.btnType == 'slot'" class="btn" :name="item.value" :row="item" />
      </span>
      <!-------------------------------------    ------------------------------------>
      <el-button
        v-if="!item.btnType"
        v-waves
        class="btn"
        :icon="item.icon"
        :type="item.type ? item.type : item.type == '' ? '' : 'primary'"
        :size="item.size || 'small'"
        @click="BtnClick(item, index)"
      >
        {{ item.label }}
      </el-button>
    </span>
  </div>
</template>

<script>
// jsjsjson
// import  from '';
import waves from '../../directive/waves'
export default {
  name: 'mmkk-button',
  directives: {
    waves
  },
  // import
  props: {
    /**
     * icon: 
     * type: 
     * size: 
     * label: 
     * value: 
     */
    btnList: {
      type: Array,
      default: () => []
    },
    //  
    isAuthority: {
      type: Boolean,
      default: true
    }

  },
  data() {
    // 
    return {
      showHide: true,
      list: []
    }
  },
  //  data
  computed: {},
  // data
  watch: {},
  //  - this
  created() {
    // console.log('', this.$router.history.current.meta.resource)
    if (this.isAuthority) {
      var newlist = this.$router.history.current.meta.resource
      newlist.forEach(element => {
        if (element.name && element.resourceType == 30) {
          element.label = element.name
          this.list.push(element)
        }
      })
    } else {
      this.list = this.btnList
    }
  },
  //  - DOM
  mounted() {
    // bus.$on('tableListData', (msg) => {
    //   this.showHide = msg
    // })
  },
  beforeCreate() { }, //  - 
  beforeMount() { }, //  - 
  beforeUpdate() { }, //  - 
  updated() { }, //  - 
  beforeDestroy() {
    // this.bus.$off('tableListData')
  }, //  - 
  destroyed() { }, //  - 
  activated() { }, // keep-alive
  // 
  methods: {
    BtnClick(row, index) {
      this.$emit('btnClick', row, index)
    },

    //  
    beforeUpload(file, item, index) {
      // console.log(file, item, index)
      // debugger
      const fileSize = file.size / 1024 / 1024 < 10
      // 
      if (fileSize) {
        if (item.returnType == 1) {
          const code = item.code
          const val = {
            file,
            item,
            code
          }
          this.$emit('btnClick', val, index)
          return false
        }
        return true
      }
      this.$message({
        message: '',
        type: 'warning'
      })
      return false
    },
    // 
    onSuccess(results, header, item, index) {
      // console.log('', results, header)
      const code = item.code
      const val = {
        results,
        header,
        item,
        code
      }
      this.$emit('btnClick', val, index)
    }
  }
}
</script>
<style lang="scss" scoped>
/* @import url(); css */
.btn-bar {
  float: right;
  padding: 3px 0;
  display: flex;
  justify-content: flex-end;
  .btn {
    margin: 0 10px;
    &:last-child {
      margin-right: 0;
    }
  }
}

.tip-bar {
  line-height: 35px;
  font-size: 14px;
  color: #333;
  span {
    margin-right: 20px;
    border-left: 3px #cf995f solid;
    padding-left: 5px;
    i {
      font-weight: bolder;
      font-style: normal;
      color: #ff0000;
    }
  }
}
</style>



